module.exports = "<div [ngSwitch]=\"loadingData\">\n  <div *ngSwitchCase=\"true\">Carregando dados...</div>\n  <div *ngSwitchCase=\"false\">\n    <form id=\"wrapper\" role=\"form\" (ngSubmit)=\"saveProfile();\" [formGroup]=\"profileForm\">\n      <h1>Feira Orgânica e Ecológica</h1>\n      <h3>Aqui a gente dá uma forcinha pra esses quiridus que cuidam da natureza produzindo produtos orgânicos e agroecológicos. É só dizer do que gosta e onde mora.</h3>\n      <p>Para que possamos fazer isso, precisamos saber se você é um fornecedor, um\n        consumidor ou os dois.</p>\n      <p>Na verdade, grande parte de nós deveria ser ambos, afinal,\n        todo mundo pode ter um vaso de manjericão em casa e, quem sabe, ter um vizinho querendo preparar aquele <a href=\"/privacy\">molho pesto</a>...</p>\n      <p>\n        A Feira Orgânica funciona baseada em sua localização. Pedimos a gentileza de\n        informar alguns dados básicos para fazer parte dessa revolução!</p>\n\n      <div>\n        <h2>Que que tu veio fazê aqui quiridu?</h2>\n        <md-button-toggle-group name=\"kind\" formControlName=\"kind\" [(ngModel)]=\"profile.kind\" color=\"primary\" >\n            <md-button-toggle value=\"cosumer\">Comprar</md-button-toggle>\n            <md-button-toggle value=\"seller\">Vender</md-button-toggle>\n            <md-button-toggle value=\"both\">Os dosx</md-button-toggle>\n            <md-button-toggle value=\"public\">Só esxpiá mesmo...</md-button-toggle>\n        </md-button-toggle-group>\n      </div>\n\n      <div class=\"address\">\n        <h2>Fala um poquinho de ti...</h2>\n        <div>\n          <md-input placeholder=\"CEP\" formControlName=\"cep\" [(ngModel)]=\"profile.cep\"></md-input>\n        </div>\n        <div>\n          <md-input placeholder=\"Estado\" formControlName=\"state\" [(ngModel)]=\"profile.state\"></md-input>\n        </div>\n        <div>\n          <md-input placeholder=\"Cidade\" formControlName=\"city\" [(ngModel)]=\"profile.city\"></md-input>\n        </div>\n        <div>\n          <md-input placeholder=\"Bairro\" formControlName=\"district\" [(ngModel)]=\"profile.district\"></md-input>\n        </div>\n        <div>\n          <md-input placeholder=\"Telefone\" formControlName=\"phone\" [(ngModel)]=\"profile.phone\"></md-input>\n        </div>\n      </div>\n\n      <div *ngIf=\"profileForm.controls.kind.value == 'cosumer' || profileForm.controls.kind.value == 'both'\">\n        <h2>Masx me conta, quanto tu come numa semana?</h2>\n        <md-button-toggle-group name=\"consume\" formControlName=\"consume\" [(ngModel)]=\"profile.consume\" color=\"primary\" >\n          <md-button-toggle value=\"20\">Até 20Kg</md-button-toggle>\n          <md-button-toggle value=\"50\">Até 50Kg</md-button-toggle>\n          <md-button-toggle value=\"200\">Até 200Kg</md-button-toggle>\n          <md-button-toggle value=\"1000 \">Até 1000Kg</md-button-toggle>\n          <md-button-toggle value=\"10000\">Acima de 10Ton</md-button-toggle>\n        </md-button-toggle-group>\n        <div [ngSwitch]=\"profileForm.controls.consume.value\">\n          <p md-line *ngSwitchCase=\"20\">Normalmente uma pessoa solteira</p>\n          <p md-line *ngSwitchCase=\"50\">Uma família pequena ou um escritório...</p>\n          <p md-line *ngSwitchCase=\"200\">Família grande</p>\n          <p md-line *ngSwitchCase=\"1000\">Restaurantes de pequeno e grande porte, escolas, eventos...</p>\n          <p md-line *ngSwitchCase=\"10000\">Restaurantes de grande porte, eventos, exportadores, ...</p>\n        </div>\n        {{profileForm.controls.consume.value}}\n      </div>\n\n      <div class=\"manufacturers\" *ngIf=\"profileForm.controls.kind.value == 'seller' || profileForm.controls.kind.value == 'both'\">\n        <h2>Aí sim hein? Produzindo orgânicos. Coisa linda!!! Explica isso direito...</h2>\n        <div>\n          <md-input placeholder=\"Site\" formControlName=\"site\" [(ngModel)]=\"profile.site\"></md-input>\n        </div>\n        <div>\n          <h3>E quanto tu produz?</h3>\n          <md-button-toggle-group name=\"production\" formControlName=\"production\" [(ngModel)]=\"profile.production\" color=\"primary\" >\n              <md-button-toggle value=\"20\">Até 20Kg</md-button-toggle>\n              <md-button-toggle value=\"100\">Até 100Kg</md-button-toggle>\n              <md-button-toggle value=\"1000\">Até 1Ton</md-button-toggle>\n              <md-button-toggle value=\"10000\">Até 10Ton</md-button-toggle>\n              <md-button-toggle value=\"100000\">Acima de 10Ton</md-button-toggle>\n          </md-button-toggle-group>\n          <div [ngSwitch]=\"profileForm.controls.production.value\">\n            <p md-line *ngSwitchCase=\"20\">Possui alguns vasos, quem sabe uma hortinha...</p>\n            <p md-line *ngSwitchCase=\"100\">Tem um pé de laranja ou de amoras...</p>\n            <p md-line *ngSwitchCase=\"1000\">Pequeno agricultor urbano, ou possui muitas árvores frutíferas...</p>\n            <p md-line *ngSwitchCase=\"10000\">Produtores e cooperativas de pequeno porte...</p>\n            <p md-line *ngSwitchCase=\"100000\">Produtores e cooperativas de grande porte...</p>\n          </div>\n        </div>\n      </div>\n\n      <button md-button color=\"accent\" type=\"submit\">Salvar</button>\n      <br>\n      <p>*Estas informações são públicas e ajudam a Feira Orgânica e seus usuários a localizarem dados mais relevantes. Para saber mais, acesse <a href=\"/privacy\">nossa política de privacidade</a></p>\n\n    </form>\n  </div>\n</div>"

